import MailchimpSubscribe from '~/node_modules/vue-mailchimp-subscribe';
export default {  
  components: {
    MailchimpSubscribe,
  },
  data() {
    return {
      closeNews : false,
      isActive : false,
      showNewsletter : true
    }
  },
  methods: {
    onError() {
    },
    onSuccess() {
    }, 
    showBox(){
      setTimeout(
        function(){
          this.isActive = true;
        }, 1000) 
    },
    setCookieNewsletter: function(){
      this.$cookies.set('newsletter',true,60 * 30);  
    },
    setCookieClose: function(){
      this.$cookies.set('closepopup',true,60 * 30);  
    },
  },
  mounted(){
    if(this.$cookies.get("newsletter")){
      this.showNewsletter = false
    }
    if(this.$cookies.get("closepopup")){
      this.showNewsletter = false
    }
    setTimeout(function(scope) {
      scope.isActive = true;
    }, 5000, this);
    // console.log(this.isActive);
  },
  created(){
      
  }
}
