const loader = require('../../assets/images/loader.gif');
export default {
    computed: {
        page() {
            return this.$store.state.content.chisiamo;
        },
        swiper() {
            this.$refs.gallery.$swiper;
        },
    },
    created() {
        // this.$store.dispatch("chisiamo");
    },
    data() {
        return {
            loader: loader,
            swiperGalleryOptions: {
                slidesPerView: 2,
                spaceBetween: 10,
                loop: true,
                pagination: {
                    el: '.swiper-pagination-gallery',
                    clickable: true,
                },
                breakpoints: {
                    1024: {
                    slidesPerView: 2,
                    spaceBetween: 10
                    },
                    768: {
                    slidesPerView: 2,
                    spaceBetween: 30
                    },
                    640: {
                    slidesPerView: 2,
                    spaceBetween: 20
                    },
                    320: {
                    slidesPerView: 1,
                    spaceBetween: 10
                    }
                }
            },
            title: 'Chi Siamo - Baround',
        }
    },
    head() {
        return {
        title: this.title,
            meta: [
                {
                hid: 'baround',
                name: 'Chi Siamo - Baround',
                content: 'La guida definitiva ai cocktail bar.'
                },
                //FB
                { hid: 'og:title', name: 'og:title', content: this.title },
                { hid: 'og:description', name: 'og:description', content: 'La guida definitiva ai cocktail bar.' },
                { hid: 'og:url', name: 'og:url', content: 'https://www.baround.it' },
                { hid: 'og:image', name: 'og:image', content: 'https://be.baround.it/wp-content/uploads/2020/10/chi-siamo.jpg' },
                //TWITTER
                { hid: 'twitter:card', name: 'twitter:card', content: 'summary_large_image' },
            ]
        }
    },
    
    methods: {
        funGalleryOption: function(){
            return {
                slidesPerView: 2,
                spaceBetween: 10,
                loop: true,
                pagination: {
                    el: '.swiper-pagination-gallery',
                    clickable: true,
                },
                breakpoints: {
                    1024: {
                    slidesPerView: 2,
                    spaceBetween: 10
                    },
                    768: {
                    slidesPerView: 2,
                    spaceBetween: 30
                    },
                    640: {
                    slidesPerView: 2,
                    spaceBetween: 20
                    },
                    320: {
                    slidesPerView: 1,
                    spaceBetween: 10
                    }
                }
            }
        },
        startGallerySwiper: function(){
            this.swiperGalleryOptions = this.funGalleryOption();
        }
    }   
};
